.header {
  // .header__top

  &__top {
    display: flex;
    align-items: center;
    width: 100%;
    height: 45px;
    background-color: #1d1e1e;
  }

  // .header__nav

  //   &__nav {
  //   }

  // .header__list

  &__list {
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }

  // .header__item

  &__item {
    position: relative;
    margin: 0 30px;
    &:after {
      content: "";
      position: absolute;
      top: calc(50% - 3.5px);
      right: -33px;
      width: 7px;
      height: 7px;
      border-radius: 50%;
      border: 2px solid #6a6a6a;
    }
    &:first-of-type {
      margin-left: 0;
    }
    &:last-of-type {
      margin-right: 0;
      &:after {
        display: none;
      }
    }
  }

  // .header__link

  &__link {
    color: #fff;
    font-size: 0.875rem;
    font-weight: 400;

    &:before {
      background: #fff;
      height: 1px;
    }
    &:hover {
      color: #fff;
      &:before {
        background: #fff;
      }
    }
  }

  // .header__right-content

  &__right-content {
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }

  // .header__text

  &__text {
    color: #fff;
    font-size: 0.81rem;
    font-weight: 400;
    margin-right: 30px;
  }

  // .header__phone

  &__phone {
    color: #009ee2;
    font-size: 1.25rem;
    font-weight: 700;
    transition: 0.4s ease-in-out;
    &:before {
      background: #009ee2;
    }

    &:hover {
      color: #009ee2;
      &:before {
        background: #009ee2;
      }
    }
  }

  // .header__main

  &__main {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 40px 0;
  }

  // .header__logo

  &__logo {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    max-width: 290px;
  }

  // .header__search

  &__search {
    input[type="search"] {
      // border-radius: 0;
      -webkit-appearance: none;
    }

    /* clears the 'X' from Internet Explorer */
    input[type="search"]::-ms-clear {
      display: none;
      width: 0;
      height: 0;
    }
    input[type="search"]::-ms-reveal {
      display: none;
      width: 0;
      height: 0;
    }

    /* clears the 'X' from Chrome */
    input[type="search"]::-webkit-search-decoration,
    input[type="search"]::-webkit-search-cancel-button,
    input[type="search"]::-webkit-search-results-button,
    input[type="search"]::-webkit-search-results-decoration {
      display: none;
    }
  }

  // .header__user-panel

  //   &__user-panel {
  //   }
  // }

  .search {
    position: relative;
    margin: 0;

    // .search__input

    &__input {
      width: 100%;
      max-width: 556px;
      height: 42px;
      line-height: 42px;
      padding: 0 25px;
      border: 1px solid #ddd;
      border-radius: 5px;
      background-color: #fff;
      color: #1d1e1e;
      font-size: 0.875rem;
      font-weight: 400;
    }

    // .search__button

    &__button {
      position: absolute;
      top: calc(50% - 9.5px);
      right: 15px;
      width: 19px;
      height: 19px;
      background: transparent;
      border: 0;
      padding: 0;
      background-image: url(./icons/search.png);
      background-repeat: no-repeat;
      background-position: center center;
    }
  }

  .user-panel {
    display: flex;
    align-items: center;
    justify-content: flex-end;

    &__user,
    &__liked,
    &__cart {
      position: relative;
      width: 30px;
      height: 30px;
      background-position: center center;
      background-repeat: no-repeat;
      margin-right: 70px;
    }
    // .user-panel__user

    &__user {
      background-image: url(./icons/user.png);
    }

    // .user-panel__liked

    &__liked {
      background-image: url(./icons/liked.png);
    }

    // .user-panel__number

    &__number {
      position: absolute;
      right: -10px;
      top: -5px;
      color: #009ee2;
      font-size: 16px;
      font-weight: 600;
    }

    // .user-panel__cart

    &__cart {
      background-image: url(./icons/cart.png);
      margin-right: 0;
    }
  }
}



