html {
  font-size: 100%;
}

img {
  width: 100%;
}

h1 {
  color: #1d1e1e;
  font-size: 2.25rem;
  font-weight: 800;
  text-align: left;
  text-transform: uppercase;
}

p {
  color: #1d1e1e;
  font-size: 0.875rem;
  font-weight: 600;
  letter-spacing: 0.35px;
  line-height: 1.56rem;
  margin-bottom: 30px;
}

a {
  text-decoration: none;
  &:hover {
    text-decoration: none;
  }
}

.button-link {
  position: relative;
  color: #009ee2;
  font-size: 0.6875rem;
  font-weight: normal;
  text-transform: uppercase;
  letter-spacing: 2px;
  &:before {
    content: "";
    width: 100%;
    height: 1px;
    position: absolute;
    bottom: 0;
    right: 0;
    background: #009ee2;
    transition: all 0.3s;
  }
}

.button-blue {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 55px;
  color: #fff;
  font-size: 1rem;
  font-weight: normal;
  text-transform: uppercase;
  border-radius: 3px;
  background-color: #0187c1;
  letter-spacing: 3.2px;
  overflow: hidden;
  transition: 0.4s ease-in-out;
  span {
    z-index: 1;
  }
  &:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: 0.4s ease-in-out;
    opacity: 1;
    background-image: linear-gradient(to left, #0187c1 0%, #3ac0ef 100%);
  }

  &:hover {
    &:after {
      opacity: 0;
    }
  }
}

input,
button {
  border: 0;
  outline: none;
  &:active,
  &:focus {
    outline: none;
  }
}

.state-line-hover {
  position: relative;
  transition: 0.3s ease;
  &:before {
    content: "";
    width: 0;
    height: 2px;
    position: absolute;
    bottom: 0;
    right: 0;
    // background: #000;
    transition: all 0.3s;
  }
  &:hover:before {
    width: 100%;
    left: 0;
    // background: #000;
  }
}

.footer-link-hover {
  position: relative;
  &:after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    background: #fff;
    height: 1px;
    top: calc(100% + 3px);
    width: 100%;
    opacity: 0;
    transition: 0.3s;
  }
  &:hover:after {
    top: 100%;
    opacity: 1;
  }
}

.slick-list {
  padding: 0 0 45px;
  z-index: 1;
}
.slick-slide,
.slick-slide:active,
.slick-slide:focus {
  outline: none;
}

.slick-prev,
.slick-next {
  position: absolute;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: #1d1e1e;

  &:before {
    content: "";
    position: absolute;
    display: inline-block;
    top: calc(50% - 3.2px);
    right: calc(50% - 3px);
    width: 6px;
    border-style: solid;
    border-color: #fff;
    border-width: 0 2px 2px 0;
    padding: 2px;
    transition: 0.3s;
  }
}

.slick-prev {
  top: -65px;
  right: 35px;
  left: inherit;
  &:before {
    transform: rotate(135deg);
    right: calc(50% - 4px);
  }
}

.slick-next {
  top: -65px;
  right: 5px;
  left: inherit;
  &:before {
    transform: rotate(-45deg);
  }
}

.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  color: transparent;
  outline: none;
  background-color: #1d1e1e;
}

.slick-dots {
  position: absolute;
  padding: 0 30px;
  bottom: 35px;
  display: block;
  width: 100%;
  margin: 0;
  list-style: none;
  text-align: left;
  z-index: 3;
}
.slick-dots li {
  width: 12px;
  height: 12px;
  margin: 0 10px;
}
.slick-dots li.slick-active {
  button {
    border-color: #009ee2;
  }
}
.slick-dots li button {
  width: 3px;
  height: 5px;
  padding: 3px;
  border-radius: 50%;
  border: 3px solid #fff;
  transition: 0.3s ease-in-out;
  &:before {
    display: none;
  }
  &:hover {
    border-color: #009ee2;
  }
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 750px;
  }
}

@media (min-width: 992px) {
  .container {
    max-width: 990px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1170px;
  }
}



