.footer {
  width: 100%;
  background-color: #1d1e1e;
  padding-top: 65px;

  // .footer__main

  &__main {
    margin-bottom: 50px;
  }

  // .footer__left

  &__left {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  // // .footer__logo

  // &__logo {
  // }

  // .footer__subtitle

  &__subtitle {
    max-width: 205px;
    color: #fff;
    font-size: 0.8125rem;
    font-weight: 600;
    line-height: 24px;
    letter-spacing: 1.06px;
    opacity: 0.5;
    margin-bottom: 50px;
  }

  // .footer__link

  &__link {
    opacity: 0.5;
    color: #fff;
    font-size: 0.75rem;
    font-weight: normal;
    text-transform: uppercase;
    letter-spacing: 0.6px;
    transition: 0.2s ease-in-out;
    &:hover {
      color: #fff;
      opacity: 1;
    }
  }

  // .footer__link-download

  &__link-download {
    color: #009be1;
    font-size: 0.875rem;
    font-weight: normal;
    margin-bottom: 15px;

    &:before {
      width: 100%;
      height: 1px;
      background: #009be1;
    }

    &:last-of-type {
      margin-bottom: 0;
    }

    &:hover {
      opacity: 0.8;
      &:before {
        opacity: 0.5;
      }
    }
  }

  // .footer__right

  &__right {
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
  }

  // .footer__list

  &__list {
    width: 25%;
  }

  // .footer__item

  &__item {
    margin-bottom: 7px;
    &.item--title {
      color: #fff;
      font-size: 0.75rem;
      font-weight: normal;
      text-transform: uppercase;
      letter-spacing: 2.4px;
      margin-bottom: 15px;
    }

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  // .footer__down

  &__down {
    position: relative;
    padding: 50px 0;
    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 15px;
      width: calc(100% - 30px);
      height: 1px;
      background: rgba(255, 255, 255, 0.1);
    }
  }

  // .footer__copyright

  &__copyright {
    color: #fff;
    font-size: 0.6875rem;
    font-weight: 600;
    letter-spacing: 0.17px;
    opacity: 0.5;
  }

  // .footer__social

  &__social {
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }

  // .footer__social-item

  &__social-item {
    margin-right: 55px;

    &:last-of-type {
      margin-right: 0;
    }
  }

  // .footer__social-link

  &__social-link {
    display: block;
    width: 25px;
    height: 20px;
    background-position: center center;
    background-repeat: no-repeat;

    &.social-vk {
      background-image: url(./social-icons/vk.png);
    }
    &.social-facebook {
      background-image: url(./social-icons/facebook.png);
    }
    &.social-instagram {
      background-image: url(./social-icons/instagram.png);
    }
  }
}



