.main {
  section {
    margin-bottom: 50px;
  }

  .head-section {
    position: relative;
    width: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: flex-start;
    border-top: 1px solid #efefef;
    padding: 50px 0 45px;

    // .head-section__text

    &__text {
      color: #1d1e1e;
      font-size: 2.25rem;
      line-height: 2.25rem;
      letter-spacing: 1.1px;
      font-weight: 800;
      text-transform: uppercase;
      margin-right: 27px;
      margin-bottom: 0;
    }

    // .head-section__link

    &__link {
      margin-bottom: 5px;
      &:hover {
        color: #009ee2;
        &:before {
          width: 0;
        }
      }
    }
  }

  // .main__home

  &__home {
    .container {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      margin-bottom: 95px;
    }
  }

  // .main__right-content

  &__right-content {
    width: 75%;
  }

  // .main__top

  &__top {
    margin-bottom: 29px;
  }

  // .main__promotions

  &__promotions {
    position: relative;
    width: 100%;
    height: 409px;
    background-color: #0086c0;
    border-radius: 5px;
    // overflow: hidden;
    &:after {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      width: 100%;
      height: 100%;
      border-radius: 5px;
      background:
        linear-gradient(
          90deg,
          rgba(255, 255, 255, 0) 0%,
          rgba(116, 221, 255, 1) 100%
        );
    }
    &:before {
      content: "";
      position: absolute;
      width: 100%;
      height: 70px;
      left: 0;
      bottom: -24px;
      background-position: center center;
      background-repeat: no-repeat;
      background-size: 100%;
      background-image: url(./images/shadow-slider.png);
      z-index: -1;
    }
  }

  // .main__bottom

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  // .main__promo

  // &__promo {
  // }
}

.promotion {
  width: 100%;
  height: 100%;

  // .promotion__item

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    height: 409px;
    padding: 60px 40px;
    outline: none;
    z-index: 2;
  }

  // .promotion__title

  &__title {
    max-width: 350px;
    color: #fff;
    font-size: 2.5rem;
    font-weight: 800;
    line-height: 3.125rem;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin-bottom: 15px;
  }

  // .promotion__sub-title

  &__sub-title {
    color: #4bd1fe;
    font-size: 1.375rem;
    font-weight: 600;
    letter-spacing: 1.1px;
    margin-bottom: 0;
  }

  // .promotion__price

  &__price {
    color: #fff;
    font-size: 1.875rem;
    font-weight: normal;
    margin-bottom: 20px;
  }

  // .promotion__button

  &__button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-width: 174px;
    height: 54px;
    border-radius: 3px;
    background-color: #1d1e1e;
    color: #fff;
    font-size: 1rem;
    font-weight: normal;
    text-transform: uppercase;
    letter-spacing: 3.2px;
  }

  // .promotion__img

  &__img {
    position: absolute;
    width: auto;
    max-width: 50%;
    height: auto;
    right: 10%;
    bottom: 0;
  }
}

.main-menu {
  position: relative;
  width: 23%;
  min-width: 263px;

  &:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 70px;
    left: 0;
    bottom: -24px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 100%;
    background-image: url(./images/shadow-promo.png);
    z-index: -1;
  }

  // .main-menu__list

  &__list {
    border-radius: 3px;
    overflow: hidden;
  }

  // .main-menu__item

  &__item {
    border-bottom: 1px solid #fff;

    &:last-of-type {
      border-bottom: 0;
    }
  }

  // .main-menu__link

  &__link {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    height: 46px;
    padding-left: 60px;
    color: #1d1e1e;
    font-size: 0.9375rem;
    font-weight: 700;
    background-color: #efefef;
    transition: 0.4s ease-in-out;

    &:hover {
      background-color: #e5e5e5;
      color: #009ee2;
      &:before {
        border-color: #009ee2;
      }
      .icon-arrow {
        border-color: #009ee2;
      }
    }

    &:before,
    &:after {
      content: "";
      position: absolute;
      transition: 0.4s ease-in-out;
    }

    &:before {
      top: calc(50% - 5.5px);
      left: 25px;
      width: 11px;
      height: 11px;
      border-radius: 50%;
      border: 3px solid #1d1e1e;
    }

    &:after {
      background-position: center center;
      background-repeat: no-repeat;
    }
  }
}
.promo {
  position: relative;
  display: block;
  width: 100%;
  max-width: 262px;
  height: 172px;
  border-radius: 5px;
  padding: 23px;
  background-color: #0086c0;

  &:before,
  &:after {
    content: "";
    position: absolute;
    background-position: center center;
    background-repeat: no-repeat;
  }

  &:before {
    width: 100%;
    height: 100%;
    right: 0;
    bottom: 0;
    background-image: url(./images/gradient.png);
  }

  &:after {
    width: 100%;
    height: 70px;
    left: 0;
    bottom: -24px;
    background-image: url(./images/shadow-promo.png);
    background-size: 100%;
    z-index: -1;
  }

  &:hover {
    .promo__button {
      width: auto;
      padding: 0 10px;
      border-radius: 10px;
      line-height: 25px;
      font-size: 0.875rem;
      background-image: none;
      z-index: 2;
      transition: width 0.5s, border-radius 0.4s, font-size 0.5s;
    }
  }

  // .promo__title

  &__title {
    max-width: 135px;
    color: #fff;
    font-size: 1.25rem;
    font-weight: normal;
    line-height: 1.625rem;
    text-transform: uppercase;
    margin-bottom: 7px;
  }

  // .promo__subtitle

  &__subtitle {
    display: inline-block;
    max-width: 135px;
    color: #4cd2fe;
    font-size: 0.875rem;
    line-height: 1rem;
    font-weight: 600;
  }

  // .promo__button

  &__button {
    position: absolute;
    left: 23px;
    bottom: 23px;
    display: block;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #1d1e1e;
    background-position: 10px 7px;
    background-repeat: no-repeat;
    background-image: url(./images/icons/arrow-right.png);
    color: #fff;
    font-weight: 600;
    font-size: 0;
    transition: width 0.3s, border-radius 0.5s;
  }

  // .promo__img

  &__img {
    position: absolute;
    width: auto;
    right: 0;
    bottom: -3px;
  }
}

.icon-arrow {
  position: absolute;
  display: inline-block;
  top: calc(50% - 4px);
  right: 18px;
  width: 6px;
  border-style: solid;
  border-color: #b2b2b2;
  border-width: 0 2px 2px 0;
  padding: 3px;
  transform: rotate(-45deg);
  transition: 0.3s;
}

