.nav {
  width: 100%;
  height: 60px;
  background-color: #efefef;
  margin-bottom: 30px;

  // .nav__catalog

  &__catalog {
    position: relative;
    width: 263px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #009ee2;
    background-image: linear-gradient(to left, #0187c1 0%, #3ac0ef 100%);
    border-radius: 3px;
    color: #fff;
    font-size: 1.125rem;
    font-weight: 600;
    line-height: 25px;
    &:hover {
      color: #fff;
    }
    &:after,
    &:before {
      content: "";
      position: absolute;
      background-position: center center;
      background-repeat: no-repeat;
    }
    &:after {
      width: 11px;
      height: 6px;
      top: calc(50% - 3px);
      right: 23px;
      background-image: url(./images/icons/arrow.png);
    }
    &:before {
      width: 25px;
      height: 18px;
      top: calc(50% - 9px);
      left: 17px;
      background-image: url(./images/icons/menu.png);
    }
  }

  // .nav__list

  &__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-left: 20px;
    height: 100%;
  }

  // .nav__item

  &__item {
    height: 100%;
  }

  // .nav__link

  &__link {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #1d1e1e;
    font-size: 0.875rem;
    font-weight: 700;
    text-transform: uppercase;
    height: 100%;
    transition: 0.3s ease-in-out;
    overflow: hidden;
    &::before {
      content: "";
      position: absolute;
      width: 0;
      height: 0;
      border-left: 10px solid transparent;
      border-right: 10px solid transparent;
      border-top: 10px solid #fff;
      top: -10px;
      right: calc(50% - 10px);
      transition: 0.2s ease-in;
    }
    &:hover {
      color: #009ee2;
      &::before {
        transition-delay: 0.1s;
        top: 0;
      }
    }
  }
}



